.Burger{
  width:100%;
  text-align: center;
  margin : auto; 
  height : 250px; 
  overflow : auto ; 
  font-size : 1.2rem ; 
  font-weight :bold; 
}

@media(min-width : 500px) and (min-height: 400px){
  .Burger{
    width : 350px;
    height: 300px;
  }
}

@media (min-width: 1000px) and (min-height : 700px){
  .Burger{
    width : 700px; 
    height : 600px;
  }
}

